import React, {useState} from 'react';
import CounterItem from "../components/counterItem";

const CounterCount = () => {
    const [counters, setCounters] = useState( [

    ])
    // console.log(props)
    // console.log(updateCount)
    const [sum, setSum] = useState(0)



    function removeAllCounters() {
        counters.length = 0
        setCounters([...counters])
    }

    // function sumAllCountersCount() {
    //     // setSum(5)
    // }

    const deleteCounter = (counter) => {
        setCounters(counters.filter(c => c.id !== counter.id))
    }

    function addCounter() {
        const newCounter = {
            id: Date.now(),
            value: 0
        }

        counters.forEach(counter => {
            // setSum( sum + count)
            // console.log(counter.value)
            if(counter.value % 2 === 0) {
                updateCount()
            }
            // setCounters([...counters])
        })
        setCounters([...counters, newCounter])
    }

    // const checKValue = (count) => {
    //     console.log(count)
    //     let value = count
    // }
    // let sum = 0
    const updateCount = (count) => {
        count += 1
    }

    return (
        <div>
            <div className="header">
                <button className="btn btn__create" onClick={addCounter}>Create</button>
                <button className="btn btn__reset" onClick={removeAllCounters} >Reset</button>
                <div className="count__block">
                    <h1>Количество счетчиков: {counters.length}</h1>
                    <h1>Sum Counts: {sum}</h1>
                </div>
            </div>
            {counters.map(counter =>
                <CounterItem updated={updateCount} deleted={deleteCounter} key={counter.id} setCounters={setCounters} counter={counter} counters={counters}/>
            )}
        </div>
    );
};

export default CounterCount;