import React, {useState} from 'react';

const CounterItem = ({counters, counter, deleted, updated}) => {
    const [count, setCount] = useState(0)
    const [str, setStr] = useState('Введено четное число')

    // setCount(counter.value)

    function increment() {
        setCount(count + 1)
        check(count + 1)
        // counter.value = count
    }

    function decrement() {
        setCount(count - 1)
        check(count - 1)
        // counter.value = count
    }

    function reset() {
        setCount(0)
        check(0)
        // counter.value = count
    }

    function check(count) {
        if(count % 2 === 0) {
            setStr('Введено четное число')
        } else {
            setStr('Введено нечетное число')
        }
        if(count < 0) {
            setCount(0)
            setStr('Введено четное число')
        }
    }



    // const [value, setValue] = useState(0)
    counter.value = count

    // console.log(counter.value)

    return (
        <div className="container">
            <h1 className="title__count" onChange={updated(count)}>{counter.value}</h1>
            <h1 className="title__str">{str}</h1>
            <div className="btn__block">
                {/*<h1>{props.counter.id}</h1>*/}
                <button onClick={increment}>+</button>
                <button onClick={reset}>Reset</button>
                <button onClick={decrement}>-</button>
                <button onClick={() => deleted(counter)}>Delete</button>
            </div>
        </div>
    );
};

export default CounterItem;